// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import { isDisableButton } from 'helpers/Offer';
import { OFFER_STEPS } from 'shared/Consts';

type Props = {
  cell: Object,
  handleApproval: Function,
  isSubmitting: boolean,
};

export const ActionButtons = ({
  cell,
  handleApproval,
  isSubmitting,
}: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offer = _get(cell, 'row.original');

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const onClickApprovalHandler = () => {
    if (
      isSubmitting ||
      isDisableButton(offer, OFFER_STEPS.awaitingSellerAction)
    )
      return;

    handleApproval(_get(offer, 'OfferId'));
    handleClose();
  };

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={handleClick}
        disabled={
          isSubmitting ||
          isDisableButton(offer, OFFER_STEPS.awaitingSellerAction)
        }
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={onClickApprovalHandler}>Accept</MenuItem>
      </Menu>
    </>
  );
};
