// @flow
import React, { useMemo } from 'react';
import type { Element, AbstractComponent } from 'react';
import { useTable } from 'react-table';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import isArray from 'lodash/isArray';
import './Style.css';

type Props = {
  columns: Array<Object>,
  data: Array<Object>,
};

const checkProps = ({ columns, data }) =>
  isArray(columns) && columns.length && isArray(data) && data.length;

const Table = (props: Props): Element<'table'> => {
  const { columns, data } = props;

  const dataMemo = useMemo(() => data, [data]);

  const {
    getTableProps,
    getTableBodyProps,
    headers,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data: dataMemo,
  });

  return (
    <table {...getTableProps()} className="nile-table">
      <thead>
        <tr>
          {headers.map(header => {
            const { key, ...restHeaderProps } = header.getHeaderProps();

            return (
              <th key={key} {...restHeaderProps}>
                {header.render('Header')}
              </th>
            );
          })}
        </tr>
      </thead>
      <tbody {...getTableBodyProps()}>
        {rows.map(row => {
          prepareRow(row);
          const { key, ...restRowProps } = row.getRowProps();

          return (
            <tr key={key} {...restRowProps}>
              {row.cells.map(cell => {
                const { key, ...restCellProps } = cell.getCellProps();

                return (
                  <td key={key} {...restCellProps}>
                    {cell.render('Cell')}
                  </td>
                );
              })}
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default (hideIfInvalidProps(checkProps)(Table): AbstractComponent<{}>);
