// @flow
import React, { useEffect } from 'react';
import type { Element } from 'react';
import List from 'components/SellerOfferList';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import _get from 'lodash/get';
import { fetchOffers, saveOfferStep } from '../redux/Actions';
import { OFFER_STEPS } from 'shared/Consts';
import { withErrorBoundary } from 'components/ErrorBoundary';

type PropsWithoutRedux = {};

type Props = {
  fetchOffers: Function,
  offers: Array<Object>,
  isLoading: boolean,
  saveOfferStep: Function,
  isSubmitting: boolean,
  isSavedStep: boolean,
};

const OfferList = (props: Props): Element<typeof List> => {
  const {
    fetchOffers,
    offers,
    isLoading,
    saveOfferStep,
    isSubmitting,
    isSavedStep,
  } = props;

  useEffect(() => {
    fetchOffers();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isSavedStep) {
      fetchOffers();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isSavedStep]);

  const handleApproval = id => {
    saveOfferStep(id, { new_step: OFFER_STEPS.paBeingCreated });
  };

  return (
    <List
      offers={offers}
      isLoading={isLoading}
      handleApproval={handleApproval}
      isSubmitting={isSubmitting}
    />
  );
};

const structuredSelector = createStructuredSelector({
  offers: state => _get(state, ['sellerOffers', 'list', 0, 'Offers']),
  isLoading: state => _get(state, ['sellerOffers', 'isLoading']),
  isSubmitting: state => _get(state, ['sellerOffer', 'isLoading']),
  isSavedStep: state => _get(state, ['sellerOffer', 'isSavedStep']),
});

const mapDispatchToProps = {
  fetchOffers,
  saveOfferStep,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(OfferList)): Element<PropsWithoutRedux>);
